<template>
  <v-container fluid class="contents">
    <v-row>
      <v-col cols="12">
        <v-row align="start" justify="center">
          <v-col cols="12">
            <!-- <v-img
            class="align-end"
            :src="require('../assets/images/information.jpg')"
          ></v-img> -->
            <p class="pt-5 title">
              홍보관 안내
            </p>
            <p class="subtitle-1 pt-5">
              서울특별시 동작구 보라매로5가길 16, 304호 (신대방동,
              보라매아카데미타워)
            </p>
            <!-- <v-img
              class="align-end"
              :src="require('../assets/images/information.jpg')"
            ></v-img> -->
          </v-col>
        </v-row>
        <v-row align="start" justify="center">
          <v-col cols="12">
            <vue-daum-map
              :appKey="appKey"
              :center.sync="center"
              :level.sync="level"
              :mapTypeId="mapTypeId"
              :libraries="libraries"
              @load="onLoad"
              @center_changed="changedCenter"
              style="height:500px;"
            />
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import VueDaumMap from "vue-daum-map";
export default {
  components: {
    VueDaumMap
  },
  data() {
    return {
      appKey: "1d3936ea3a69184102069887533d44bf", // 테스트용 appkey
      center: { lat: 37.49108138232143, lng: 126.92380927995359 }, // 지도의 중심 좌표
      level: 4, // 지도의 레벨(확대, 축소 정도),
      mapTypeId: VueDaumMap.MapTypeId.NORMAL, // 맵 타입
      libraries: ["services"], // 추가로 불러올 라이브러리
      map: null // 지도 객체. 지도가 로드되면 할당됨.
    };
  },
  methods: {
    onLoad(map) {
      this.map = map;
      // 마커가 표시될 위치입니다
      let markerPosition = new kakao.maps.LatLng(
        37.49130672142065,
        126.92394474285184
      );

      var content =
        '<div class="customoverlay">' +
        '  <a href="http://kko.to/EqyhrLm0p" target="_blank">' +
        '    <span class="title">보라매 센트럴<br />바움 홍보관</span>' +
        "  </a>" +
        "</div>";

      // 커스텀 오버레이를 생성합니다
      var customOverlay = new kakao.maps.CustomOverlay({
        map: map,
        position: markerPosition,
        content: content,
        yAnchor: 1
      });
    },
    changedCenter(v) {
      // console.log(
      //   "위도[" +
      //     this.map.getCenter().getLat() +
      //     "], 경도[" +
      //     this.map.getCenter().getLng() +
      //     "], 지도레벨[" +
      //     this.map.getLevel() +
      //     "], mapTypeId[" +
      //     this.map.getMapTypeId() +
      //     "]"
      // );
    }
  }
};
</script>
<style>
.customoverlay {
  position: relative;
  bottom: 13px;
  border-radius: 6px;
  border: 1px solid #ccc;
  border-bottom: 2px solid #ddd;
  float: left;
}
.customoverlay:nth-of-type(n) {
  border: 0;
  box-shadow: 0px 1px 2px #888;
}
.customoverlay a {
  display: block;
  text-decoration: none;
  color: #000;
  text-align: center;
  border-radius: 6px;
  font-size: 12px;
  font-weight: bold;
  overflow: hidden;
  background: #d95050;
  background: #d95050
    url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/arrow_white.png)
    no-repeat right 14px center;
}
.customoverlay .title {
  display: block;
  text-align: center;
  background: #fff;
  margin-right: 35px;
  padding: 10px 15px;
  font-size: 12px;
  font-weight: bold;
}
.customoverlay:after {
  content: "";
  position: absolute;
  margin-left: -12px;
  left: 50%;
  bottom: -12px;
  width: 22px;
  height: 12px;
  background: url("https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white.png");
}
</style>



